
.nav {
    background-color: rgb(21, 117, 131);
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}
.nav ul{
    list-style: none;
    display: flex;
    gap: 1rem;
}
.nav a{
    color: inherit;
}

.header {
    align-items: center;
}

.reviews {
    display: flex; 
    flex-direction: column; 
    justify-content: center;
}

.name {
    text-decoration: underline;
}

.logo {
    position: absolute;
    left: 48%;
}

.main {
    display: flex; 
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    line-height: 0.8;
}

.list {
    line-height: 0.5;
}

.topofPage{
    text-decoration: underline;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 0.8;
}